@import url(https://fonts.googleapis.com/css2?family=Shippori+Mincho+B1&display=swap);
* {
  padding: 0;
  margin: 0;
  background-color: #dddddd;
}

h1 {
  font-family: 'Shippori Mincho B1', serif;
  text-align: center;
  font-size: 50px;
  padding-top: 10px;
  padding-bottom: 20px;
  background-color: transparent;
}

h2,
em {
  font-family: 'Shippori Mincho B1', serif;
  text-align: center;
  background-color: transparent;
}

.title {
  background-color: #666666;
}

.subtitle {
  background-color: #999999;
  padding: 80px;
}

* {
    margin: 0;
    padding: 0;
}

.placeholder {
    max-width: 80%;
    margin: 40px 20% 0 20%;
    display: grid;
    grid-template-rows: 3fr 1fr;
    grid-template-areas: 
    'box box box box box box box box box box box box box box box box clear'
    'submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit submit'
    ;
}

.textbox {
    padding-bottom: 100px;
    align-self: center;
    grid-area: box;
    background-color: #cccccc;
    resize: none;
}

.textbox:focus {
    background-color: #dddddd;
}

.clear {
    font-family: sans-serif;
    font-size: 30px;
    display: block;
    padding: 20px;
    float: left;
    background-color: #999999;
    font-weight: 70;
    border: none;
    grid-area: clear;
}

.clear:hover {
    background-color: #aaaaaa;
}

.clear:active {
    background-color: #ccc;
}

.submit {
    background-color: #778;
    grid-area: submit;
    border: none;
}

.submit:hover {
    background-color: #889;
}

.submit:active {
    background-color: #99a;
}
.post {
    background-color: #77a;
    padding: 30px;
    padding-bottom: 10px;
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 40px;
    margin-bottom: 50px;
    border-radius: 10px;
}

.btn {
    width: 30px;
    height: 30px;
    display: block;
    margin: 0 0 20px -20px;
    border: none;
    border-radius: 7px;
}

.up {
    margin-top: -50px;
    margin-bottom: 10px;
    background-color: #008b8b;
}

.up:hover {
    background-color: #009b9b;
}

.down {
    margin-top: 10px;
    background-color: #fa8072;
}

.down:hover {
    background-color: #fd9082;
}

.votes {
    margin: 0;
    background-color: transparent;
    font-size: 28px;
    margin-left: -12px;
}

